define("designmonster/about/template", ["exports"], function (exports) {
  exports["default"] = Ember.HTMLBars.template({ "id": "PlxHV8+M", "block": "{\"statements\":[[\"open-element\",\"h1\",[]],[\"flush-element\"],[\"text\",\"About Us\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"Design Monster\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel panel-default\"],[\"flush-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-heading\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"h3\",[]],[\"static-attr\",\"class\",\"panel-title\"],[\"flush-element\"],[\"text\",\"We Are Design Monster\"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-body\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"What We Do:\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"We are a product launch and start-up consultancy.  We have the knowledge and the resources to make your ideas into real products.  Our stepped programs offer the flexability to get your products off the ground.  Whether you've got just a sketch, some scribbles or just a notion, we can move you toward the process of bringing your dreams to life.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Anything is possible with our network of experts.  From Illustrators and 3d Artists, Web Developers and Graphic Designers, Patent Attorneys and Product Marketers, Engineers and Prototype Designers.  We can offer you short or long term goals with realistic expectations. And these aren't just outside companies we Google and then hire.  We've worked with them throughout the years and we know what they can do.  Design Monster's resources are a network of collaberators and made up of passionate people.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"We offer product development for all stages.  If one of our packages doesn't quite fit, \"],[\"block\",[\"link-to\"],[\"contact\"],null,3],[\"text\",\"!  We'll customize a product launch that fits your needs.\"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel panel-default\"],[\"flush-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-heading\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"h3\",[]],[\"static-attr\",\"class\",\"panel-title\"],[\"flush-element\"],[\"text\",\"Calling All Inventors!\"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-body\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"Design Monster Was Founded By Inventors!\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Individual inventors create revolutionary products.  We know that because we strive to create new and inovative products every day.  With multiple patents in our portfolio, and more pending, we here at Design Monster know that regular people with extraordinary ideas can change the world.  That's what we want for you and that's what we're here for.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Your ideas are safe with us.  Non-disclosure agreements are standard here.  We don't want your idea for ourselves, we want to help you to bring it to everyone!  As inventors we know that the process can be overwhelming.  We strive for clarity every step of the way.  Always ask us questions.  Inventors question everything!  That need to ask why shouldn't stop just because you've got your idea down on paper.  We'll give you regular updates and we'll never be out of reach.  In fact, if possible, we reccomend that face-to-face happen on a regular basis.  Your inventions are personal things and you are striving to bring them into thsi world.  The process should be personal too.\"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel panel-default\"],[\"flush-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-heading\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"h3\",[]],[\"static-attr\",\"class\",\"panel-title\"],[\"flush-element\"],[\"text\",\"Design Monster Does More Than Just Help Inventors.\"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-body\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"We Love Entrapenuers.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Start-ups are our favorite thing ever!  We offer consulting to help streamline start-ups and really get your business going in the right direction.  It's difficult to be successful.  It's challenging to know how.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Start-ups ARE inventions.  You've had an idea.  You know that you can make that idea a success.  You've saved, you've worked really hard, and you've been patient.  Now that idea is taking shape and you're going to go for it!  We've been there.  In fact, launching a business involves many of the same steps as launching a product because it is a product.  It's your product.  \"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Small businesses can thrive.  They can also flounder.  Preperation, clear goals, and experience make the difference.  We are passionate about community based, wonderful start-ups that want to bring energy and ideas to life.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"If you have, or are thinking about a start-up, \"],[\"block\",[\"link-to\"],[\"contact\"],null,2],[\"text\",\".  We would love to hear about it and get involved.  \"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel panel-default\"],[\"flush-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-heading\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"h3\",[]],[\"static-attr\",\"class\",\"panel-title\"],[\"flush-element\"],[\"text\",\"Thinking About Design Monster?\"],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n  \"],[\"open-element\",\"div\",[]],[\"static-attr\",\"class\",\"panel-body\"],[\"flush-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"Here's What We Suggest:\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Browse our Resources (link to this later) page.  You'll find examples of past projects, along with some of the steps those inventors took when they decided to move forward.  You'll find real people saying real things about what we've done for them.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"If you decide to go for it, \"],[\"block\",[\"link-to\"],[\"contact\"],null,1],[\"text\",\".  Give us a little information about what you're looking for and ask any questions you may have.  We can even meet for coffee.  This will just be an informal, non-specific talk.  Your ideas stay with you until you hire us, but we encourage everyone to have a clear understanding of how the process works.\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Submit an idea.  After our initial talk we'll sign an NDA(Non-Disclosure-Agreement) and we can start to hash out the specifics of your idea.  The initial flat fee gets you a lot.  We research your idea and compare it to existing products.  We sit down with you and map out a clear, time-boxed plan according to just how much you want to do with it and we lay out costs for every step.  This could include modeling, both computer and real-world, engineering, prototyping, patenting, and anything in between.  It's really up to you.  What do you want to do with your product?\"],[\"close-element\"],[\"text\",\"\\n    \"],[\"open-element\",\"li\",[]],[\"static-attr\",\"class\",\"list-group-item\"],[\"flush-element\"],[\"text\",\"Above all, be excited!  Think of all those products out there.  Think of how they were made.  What caused them to be invented in the first place?  Why do they exist?  Imagine a world without filtered water or electricity.  But it doesn't even have to be that big!  Think about regular things, things like clothes hangers or remote controls, dog collars or reusable grocery bags.  All of them, invented.  Patented.  Produced.  Will your idea stay an idea, or will it get made?  It's up to you!  \"],[\"open-element\",\"br\",[]],[\"flush-element\"],[\"close-element\"],[\"block\",[\"link-to\"],[\"contact\"],null,0],[\"close-element\"],[\"text\",\"\\n  \"],[\"close-element\"],[\"text\",\"\\n\"],[\"close-element\"],[\"text\",\"\\n\"]],\"locals\":[],\"named\":[],\"yields\":[],\"blocks\":[{\"statements\":[[\"text\",\"CONTACT US\"]],\"locals\":[]},{\"statements\":[[\"text\",\"CONTACT US\"]],\"locals\":[]},{\"statements\":[[\"text\",\"CONTACT US \"]],\"locals\":[]},{\"statements\":[[\"text\",\"CONTACT US\"]],\"locals\":[]}],\"hasPartials\":false}", "meta": { "moduleName": "designmonster/about/template.hbs" } });
});